hr {
  margin: 25px 0; }

.footer {
  height: 88px;
  position: relative;
  font-family: "Calibri", sans-serif; }
  .footer .line-3 {
    position: absolute;
    bottom: 45px;
    width: 100%;
    z-index: 20;
    height: 50px;
    background: url("../images/line-3.png") no-repeat 50% 45%;
    background-size: cover; }
  .footer .footer-inner {
    background: #282828;
    z-index: 50;
    height: 100%;
    position: relative;
    overflow: auto; }
    .footer .footer-inner:after {
      content: "";
      display: table;
      clear: both; }
    .footer .footer-inner ul {
      display: block;
      text-align: center;
      margin-top: 17px;
      letter-spacing: 0.05px;
      color: #fff; }
      .footer .footer-inner ul li {
        display: inline-block;
        font-weight: 200;
        border-right: 2px #feb22f solid;
        padding: 0 26px 0 22px;
        font-size: 13px; }
        .footer .footer-inner ul li:last-child {
          border-right: none; }
    .footer .footer-inner .left, .footer .footer-inner .right {
      margin-top: 20px;
      font-size: 14px;
      letter-spacing: 0.15px;
      display: inline-block;
      color: #feb22f;
      vertical-align: top; }
    .footer .footer-inner .left {
      float: left; }
    .footer .footer-inner .right {
      float: right; }
      .footer .footer-inner .right a {
        color: #feb22f; }
        .footer .footer-inner .right a:hover {
          border-bottom: 1px #fff solid;
          color: #fff; }
